export default function SQLAgentChat() {
  const [messages, setMessages] = useState([
    {
      id: 1,
      content: "Hello! I'm your Schema-Aware AI SQL Agent. You can:\n\nâ€¢ Ask natural language questions about your data\nâ€¢ Use SQL queries directly\nâ€¢ Create visualizations using @SHIP_NAME=value @NOMENCLATURE=value format\n\nWhat would you like to explore today?",
      isUser: false,
      timestamp: new Date().toLocaleTimeString(),
      type: 'text'
    }
  ]);
  
  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const scrollAreaRef = useRef(null);

  const scrollToBottom = () => {
    if (scrollAreaRef.current) {
      const scrollContainer = scrollAreaRef.current.querySelector('[data-radix-scroll-area-viewport]');
      if (scrollContainer) {
        scrollContainer.scrollTop = scrollContainer.scrollHeight;
      }
    }
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const addMessage = (content, isUser = false, type = 'text', data = null) => {
    const newMessage = {
      id: Date.now(),
      content,
      isUser,
      timestamp: new Date().toLocaleTimeString(),
      type,
      data
    };
    setMessages(prev => [...prev, newMessage]);
    return newMessage;
  };

  const handleApiRequest = async (query) => {
    try {
      const response = await fetch(API_ENDPOINTS.ask, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ question: query })
      });
      
      const data = await response.json();
      
      if (response.ok) {
        if (data.error) {
          addMessage(`Error: ${data.message}`, false, 'error');
        } else {
          let resultMessage = "Query executed successfully!";
          
          if (data.generated_sql) {
            addMessage("", false, 'sql', data.generated_sql);
          }
          
          if (data.result && Array.isArray(data.result) && data.result.length > 0) {
            addMessage("", false, 'table', { data: data.result, title: "Query Results" });
          } else {
            addMessage("Query executed successfully, but no records were found.", false, 'warning');
          }
        }
      } else {
        addMessage(`API Error: ${data.message || 'Unknown error'}`, false, 'error');
      }
    } catch (error) {
      addMessage(`Connection error: ${error.message}`, false, 'error');
    }
  };

  const handleChatRequest = async (query) => {
    try {
      const response = await fetch(API_ENDPOINTS.chat, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ query })
      });
      
      const data = await response.json();
      
      if (response.ok) {
        if (data.message && data.message.startsWith("CLARIFY:")) {
          const clarification = data.message.replace("CLARIFY:", "").trim();
          addMessage(`I need more details: ${clarification}`, false, 'warning');
        } else if (data.error) {
          addMessage(`${data.message}`, false, 'error');
        } else {
          if (data.generated_sql) {
            addMessage("", false, 'sql', data.generated_sql);
          }
          
          if (data.result && Array.isArray(data.result) && data.result.length > 0) {
            addMessage("", false, 'table', { data: data.result, title: "Query Results" });
          } else {
            addMessage("Query executed successfully, but no records were found.", false, 'warning');
          }
        }
      } else {
        addMessage(`API Error: ${data.message || 'Unknown error'}`, false, 'error');
      }
    } catch (error) {
      addMessage(`Connection error: ${error.message}`, false, 'error');
    }
  };

  const handleDrishtiRequest = async (variables) => {
    try {
      const params = new URLSearchParams({
        nomenclature: variables.NOMENCLATURE,
        ship_name: variables.SHIP_NAME
      });
      
      const response = await fetch(`${API_ENDPOINTS.drishti}?${params}`);
      const data = await response.json();
      
      if (response.ok) {
        if (data.error) {
          addMessage(`API Error: ${data.message}`, false, 'error');
        } else {
          // Display component information
          const componentInfo = `Component: ${data.component_name}\nID: ${data.component_id}\nNomenclature: ${data.nomenclature || 'N/A'}`;
          addMessage(componentInfo, false, 'success');
          
          // Mock reliability stats for demonstration
          const mockStats = {
            average: 85.7,
            min: 72.3,
            max: 98.1,
            total: 15
          };
          
          addMessage("", false, 'reliability', mockStats);
          addMessage("âœ… Visualization data fetched successfully! In a full implementation, this would show an interactive network diagram.", false, 'success');
        }
      } else {
        addMessage(`HTTP ${response.status}: ${data.detail || 'Request failed'}`, false, 'error');
      }
    } catch (error) {
      addMessage(`Connection error: ${error.message}`, false, 'error');
    }
  };

  const handleSubmit = async () => {
    if (!input.trim()) return;
    
    const userMessage = input.trim();
    setInput('');
    setIsLoading(true);
    
    // Add user message
    addMessage(userMessage, true);
    
    // Determine query mode and process
    const { mode, variables } = getQueryMode(userMessage);
    
    // Add mode indicator
    const modeIcons = {
      api: <Database className="h-4 w-4" />,
      chat: <MessageCircle className="h-4 w-4" />,
      drishti: <Eye className="h-4 w-4" />
    };
    
    const modeColors = {
      api: 'bg-blue-100 text-blue-800',
      chat: 'bg-green-100 text-green-800',
      drishti: 'bg-purple-100 text-purple-800'
    };
    
    addMessage("", false, 'mode', { mode, icon: modeIcons[mode], color: modeColors[mode] });
    
    try {
      switch (mode) {
        case 'api':
          await handleApiRequest(userMessage);
          break;
        case 'chat':
          await handleChatRequest(userMessage);
          break;
        case 'drishti':
          await handleDrishtiRequest(variables);
          break;
      }
    } catch (error) {
      addMessage(`Unexpected error: ${error.message}`, false, 'error');
    }
    
    setIsLoading(false);
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSubmit();
    }
  };

  const renderMessage = (message) => {
    switch (message.type) {
      case 'table':
        return <ResultsTable data={message.data.data} title={message.data.title} />;
      case 'sql':
        return <SQLDisplay sql={message.data} />;
      case 'reliability':
        return <ReliabilityStats stats={message.data} />;
      case 'mode':
        return (
          <div className="flex items-center gap-2 my-2">
            <Badge className={`${message.data.color} flex items-center gap-1`}>
              {message.data.icon}
              {message.data.mode.toUpperCase()} Mode
            </Badge>
          </div>
        );
      case 'error':
        return (
          <Alert className="border-red-200 bg-red-50">
            <AlertCircle className="h-4 w-4 text-red-600" />
            <AlertDescription className="text-red-800">{message.content}</AlertDescription>
          </Alert>
        );
      case 'warning':
        return (
          <Alert className="border-yellow-200 bg-yellow-50">
            <AlertCircle className="h-4 w-4 text-yellow-600" />
            <AlertDescription className="text-yellow-800">{message.content}</AlertDescription>
          </Alert>
        );
      case 'success':
        return (
          <Alert className="border-green-200 bg-green-50">
            <CheckCircle2 className="h-4 w-4 text-green-600" />
            <AlertDescription className="text-green-800">{message.content}</AlertDescription>
          </Alert>
        );
      default:
        return <MessageBubble message={message.content} isUser={message.isUser} timestamp={message.timestamp} />;
    }
  };

  return (
    <div className="flex flex-col h-screen bg-gradient-to-br from-blue-50 via-white to-purple-50">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 p-4 shadow-sm">
        <div className="max-w-4xl mx-auto">
          <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-2">
            <Zap className="h-6 w-6 text-blue-500" />
            Schema-Aware AI SQL Agent
          </h1>
          <p className="text-gray-600 mt-1">Ask questions, run queries, or create visualizations</p>
        </div>
      </div>

      {/* Messages */}
      <ScrollArea className="flex-1 p-4" ref={scrollAreaRef}>
        <div className="max-w-4xl mx-auto">
          {messages.map((message) => (
            <div key={message.id} className="mb-4">
              {renderMessage(message)}
            </div>
          ))}
          
          {isLoading && (
            <div className="flex items-center gap-2 text-gray-500 mb-4">
              <Loader2 className="h-4 w-4 animate-spin" />
              Processing your request...
            </div>
          )}
        </div>
      </ScrollArea>

      {/* Input */}
      <div className="bg-white border-t border-gray-200 p-4 shadow-sm">
        <div className="max-w-4xl mx-auto">
          <div className="flex items-center gap-2">
            <Input
              value={input}
              onChange={(e) => setInput(e.target.value)}
              onKeyDown={handleKeyPress}
              placeholder="Ask a question, write SQL, or use @SHIP_NAME=value @NOMENCLATURE=value..."
              className="flex-1"
              disabled={isLoading}
            />
            <Button 
              onClick={handleSubmit} 
              disabled={!input.trim() || isLoading}
              className="bg-blue-500 hover:bg-blue-600 text-white"
            >
              {isLoading ? <Loader2 className="h-4 w-4 animate-spin" /> : <Send className="h-4 w-4" />}
            </Button>
          </div>
          
          {/* Help text */}
          <div className="mt-2 text-xs text-gray-500 flex items-center gap-4">
            <span>ðŸ’¡ Examples:</span>
            <span>Natural language: "Show top customers"</span>
            <span>SQL: "SELECT * FROM customers"</span>
            <span>Visualization: "@SHIP_NAME=Enterprise @NOMENCLATURE=NCC-1701"</span>
          </div>
        </div>
      </div>
    </div>
  );
}